@use "variables" as v;
@use "base/mixins" as m;

html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: v.$fuente_principal;
  font-size: 1.6rem;
  &.thanks {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &.fijar-body {
    overflow: hidden;
  }

  &.body-scroll {
    padding-top: 8.6rem;
  }
}

p {
  margin-block-start: 1em;
  margin-block-end: 1em;
  font-size: 1.2rem;
  color: v.$oscuro;
  line-height: 1.5;
  @include m.tablet {
    font-size: 1.6rem;
  }
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

.boton {
  background-color: v.$primario;
  color: v.$oscuro;
  padding: 1rem 3rem;
  margin-top: 1rem;
  font-size: 1.5rem;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: bold;
  border-radius: 0.5rem;
  text-align: center;
  border: none;
  transition: background-color 500ms;

  &:hover {
    background-color: v.$primarioOscuro;
    transition: background-color 500ms;
  }

  @include m.tablet {
    font-size: 2rem;
  }
}

h1,
h2,
h3 {
  text-align: center;
}

h1 {
  font-size: 2.2rem;
  font-weight: bold;
  @include m.tablet {
    font-size: 3rem;
  }
}

h2 {
  font-weight: bold;
  line-height: 1.15;
  margin-block-start: 0.83em;
  margin-block-end: 0.83em;
  font-size: 2rem;
  @include m.tablet {
    font-size: 2.8rem;
  }
}

h3 {
  font-size: 1.5rem;
  font-weight: 700;
  margin-block-start: 1em;
  margin-block-end: 1em;
  line-height: 1.15;
  @include m.tablet {
    font-size: 1.8rem;
  }
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}
