html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Krub", sans-serif;
  font-size: 1.6rem;
}
body.thanks {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
body.fijar-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 8.6rem;
}

p {
  margin-block-start: 1em;
  margin-block-end: 1em;
  font-size: 1.2rem;
  color: #212121;
  line-height: 1.5;
}
@media (min-width: 768px) {
  p {
    font-size: 1.6rem;
  }
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

.boton {
  background-color: #f9ae0c;
  color: #212121;
  padding: 1rem 3rem;
  margin-top: 1rem;
  font-size: 1.5rem;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: bold;
  border-radius: 0.5rem;
  text-align: center;
  border: none;
  transition: background-color 500ms;
}
.boton:hover {
  background-color: #bd850e;
  transition: background-color 500ms;
}
@media (min-width: 768px) {
  .boton {
    font-size: 2rem;
  }
}

h1,
h2,
h3 {
  text-align: center;
}

h1 {
  font-size: 2.2rem;
  font-weight: bold;
}
@media (min-width: 768px) {
  h1 {
    font-size: 3rem;
  }
}

h2 {
  font-weight: bold;
  line-height: 1.15;
  margin-block-start: 0.83em;
  margin-block-end: 0.83em;
  font-size: 2rem;
}
@media (min-width: 768px) {
  h2 {
    font-size: 2.8rem;
  }
}

h3 {
  font-size: 1.5rem;
  font-weight: 700;
  margin-block-start: 1em;
  margin-block-end: 1em;
  line-height: 1.15;
}
@media (min-width: 768px) {
  h3 {
    font-size: 1.8rem;
  }
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.titulo .titulo-guion {
  display: none;
}
.titulo .titulo-texto {
  margin-top: 1rem;
  display: block;
}
@media (min-width: 768px) {
  .titulo {
    margin-left: auto;
    margin-right: auto;
  }
  .titulo .titulo-guion {
    display: inline;
  }
  .titulo .titulo-texto {
    margin-top: 0;
    display: inline;
  }
}

header {
  margin-top: 2rem;
  margin-bottom: 1rem;
}
header h1 {
  font-size: 1.3rem;
}
header .header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
header img {
  width: 25vw;
}
@media (min-width: 480px) {
  header h1 {
    font-size: 1.5rem;
    padding-left: 1rem;
  }
  header .header {
    flex-direction: row;
  }
}
@media (min-width: 768px) {
  header h1 {
    font-size: 2rem;
    text-align: start;
    padding-left: 3rem;
  }
  header span {
    display: block;
  }
  header img {
    width: 15rem;
  }
}
@media (min-width: 992px) {
  header {
    margin-top: 3rem;
    margin-bottom: 2rem;
  }
}

.galeria {
  padding: 5rem 0;
}

.galeria-imagenes {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
.galeria-imagenes li:hover {
  -webkit-box-shadow: 0px 0px 5px 5px rgba(100, 107, 117, 0.49);
  -moz-box-shadow: 0px 0px 5px 5px rgba(100, 107, 117, 0.49);
  box-shadow: 0px 0px 10px 5px rgba(100, 107, 117, 0.49);
}
@media (min-width: 480px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.nav-bg {
  background-color: #122542;
}

.navegacion-principal {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.blue-bar {
  background-color: #122542;
  height: 4rem;
}
@media (min-width: 768px) {
  .blue-bar {
    height: 4.5rem;
  }
}

@media (min-width: 768px) {
  .navegacion-principal {
    flex-direction: row;
    justify-content: space-around;
  }
}
.navegacion-principal a {
  display: block;
  text-align: center;
  color: #ffffff;
  text-decoration: none;
  font-size: 1.5rem;
  font-weight: bold;
  padding: 1rem;
}
@media (min-width: 768px) {
  .navegacion-principal a {
    font-size: 2rem;
  }
}

.navegacion-principal a:hover {
  background-color: #f9ae0c;
  color: #212121;
}

.footer {
  text-align: center;
  margin-top: auto;
}

.hero .boton {
  width: auto;
}
.hero .boton:hover {
  color: inherit;
}

.hero {
  background-image: url(../img/hero.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 22.5rem;
  position: relative;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .hero {
    height: 25rem;
  }
}

.contenido-hero {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.6);
  /* Sintaxis anterior */
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.contenido-hero span {
  margin-top: 1rem;
  display: block;
}
@media (min-width: 768px) {
  .contenido-hero span {
    margin-top: 0;
    display: inline;
  }
}

.contenido-hero p {
  color: #ffffff;
  margin-top: 0;
}

.sombra {
  -webkit-box-shadow: 0px 0px 10px 5px rgba(100, 107, 117, 0.49);
  -moz-box-shadow: 0px 0px 10px 5px rgba(100, 107, 117, 0.49);
  box-shadow: 0px 0px 10px 5px rgba(100, 107, 117, 0.49);
  background-color: #ffffff;
  padding: 2rem;
  border-radius: 1rem;
}

.servicio {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 5rem;
}
@media (min-width: 768px) {
  .servicio {
    padding: 0 2rem;
    display: grid;
  }
}

.servicio h3 {
  color: #122542;
  font-weight: bold;
  text-align: center;
}

.servicio p {
  line-height: 1.5;
  text-align: center;
}

.servicio .iconos {
  height: 10rem;
  width: 10rem;
  background-color: #f9ae0c;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
}
.servicio .iconos .icon {
  height: 3.5rem;
}
@media (min-width: 768px) {
  .servicio .iconos .icon {
    height: 4.5rem;
    width: auto;
    transition: height 500ms linear;
  }
  .servicio .iconos .icon:hover {
    height: 6rem;
    transition: height 500ms linear;
  }
}
@media (min-width: 768px) {
  .servicio .iconos {
    height: 15rem;
    width: 15rem;
  }
}

@media (min-width: 768px) {
  .servicios {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    /* 1fr = fraction repeat(3, 1fr) */
    column-gap: 2rem;
  }
}
.formulario {
  background-color: #122542;
  width: min(60rem, 100%);
  margin: 0 auto 5rem auto;
  padding: 2rem;
  border-radius: 1rem;
}

.formulario fieldset {
  border: none;
  padding: 0 0;
}

.formulario legend {
  text-align: center;
  font-size: 1.5rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 1rem;
  color: #f9ae0c;
}
@media (min-width: 768px) {
  .formulario legend {
    font-size: 1.8rem;
    margin-bottom: 2rem;
  }
}

.boton {
  width: 100%;
}

@media (min-width: 768px) {
  .contenedor-campos {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto auto 20rem;
    column-gap: 1rem;
  }

  .campo:nth-child(3),
.campo:nth-child(4) {
    grid-column: 1/3;
  }

  .submit {
    display: flex;
    justify-content: flex-end;
  }

  input[type=submit] {
    width: 50%;
  }
}
.campo {
  margin-bottom: 1rem;
}

.campo label {
  font-size: 1.4rem;
  color: #ffffff;
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
}
@media (min-width: 768px) {
  .campo label {
    font-size: 1.6rem;
  }
}

.campo input,
.campo textarea {
  font-size: 1.3rem;
  width: 100%;
  border: none;
  padding: 1rem;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .campo input,
.campo textarea {
    font-size: 1.5rem;
  }
}

.campo textarea {
  height: 15rem;
  resize: none;
}

.form-check-label p {
  color: #ffffff;
  margin: unset;
}
.form-check-label p:hover {
  color: #f9ae0c;
}

.invalid-feedback {
  color: #f9ae0c;
}

.form-check-input.is-invalid, .was-validated .form-check-input:invalid {
  border-color: #f9ae0c;
  border: 3px solid #f9ae0c;
}

@media (min-width: 768px) {
  .form-check {
    margin-top: 1rem;
    grid-column: 1/3;
  }
  .form-check p.aviso {
    font-size: 1.4rem;
  }
}

.invalid-feedback-field {
  padding-top: 0.5rem;
}

@media (min-width: 768px) {
  .btn-sendd {
    margin-top: 5rem;
  }
}

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}

.checkmark__circle {
  stroke-dasharray: 166;
  stroke-dashoffset: 166;
  stroke-width: 4;
  stroke-miterlimit: 10;
  stroke: #122542;
  fill: none;
  animation: stroke 0.6s cubic-bezier(0.65, 0, 0.45, 1) forwards;
}

.checkmark {
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  display: block;
  stroke-width: 2;
  stroke: #fff;
  stroke-miterlimit: 10;
  margin: 10% auto;
  box-shadow: inset 0px 0px 0px #122542;
  animation: fill 0.4s ease-in-out 0.4s forwards, scale 0.3s ease-in-out 0.9s both;
}
@media (min-width: 768px) {
  .checkmark {
    width: 10rem;
    height: 10rem;
    margin: 5% auto;
  }
}

.checkmark__check {
  transform-origin: 50% 50%;
  stroke-dasharray: 48;
  stroke-dashoffset: 48;
  animation: stroke 0.3s cubic-bezier(0.65, 0, 0.45, 1) 0.8s forwards;
}

.thanks-msg {
  text-align: center;
  padding: 0 2rem;
  margin-bottom: 2rem;
}
.thanks-msg h2 {
  font-size: 3rem;
  font-weight: bold;
  margin: 0;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .thanks-msg h2 {
    font-size: 4rem;
    margin-bottom: 6rem;
  }
}
@media (min-width: 768px) {
  .thanks-msg {
    padding: 0 0;
    margin-bottom: 0;
  }
}

.thanks-phone {
  margin-top: 4rem;
}

.thanks-home {
  font-weight: bold;
}
.thanks-home:hover {
  color: #f9ae0c;
}

.thanks-section {
  margin-top: auto;
  margin-bottom: auto;
}

@keyframes stroke {
  100% {
    stroke-dashoffset: 0;
  }
}
@keyframes scale {
  0%, 100% {
    transform: none;
  }
  50% {
    transform: scale3d(1.1, 1.1, 1);
  }
}
@keyframes fill {
  100% {
    box-shadow: inset 0px 0px 0px 60px #122542;
  }
}
.social-network {
  display: flex;
  justify-content: center;
}
.social-network a {
  margin: 0 0.5rem 3rem 0.5rem;
  opacity: 0.75;
  transition: opacity 0.3s;
}
.social-network a:hover {
  opacity: 1;
}