@use "base/variables" as v;
@use "base/mixins" as m;

.galeria {
  padding: 5rem 0;
}

.galeria-imagenes {
  li:hover {
    -webkit-box-shadow: 0px 0px 5px 5px rgba(100, 107, 117, 0.49);
    -moz-box-shadow: 0px 0px 5px 5px rgba(100, 107, 117, 0.49);
    box-shadow: 0px 0px 10px 5px rgba(100, 107, 117, 0.49);
  }

  @include m.resetear-lista;
  @include m.grid(1, 2rem);
  @include m.telefono {
    @include m.grid(1, 2rem);
  }
  @include m.tablet {
    @include m.grid(1, 2rem);
  }
  @include m.desktop {
    @include m.grid(2, 2rem);
  }
}
