@use "base/variables" as v;
@use "base/mixins" as m;

.sombra {
  -webkit-box-shadow: 0px 0px 10px 5px rgba(100, 107, 117, 0.49);
  -moz-box-shadow: 0px 0px 10px 5px rgba(100, 107, 117, 0.49);
  box-shadow: 0px 0px 10px 5px rgba(100, 107, 117, 0.49);
  background-color: v.$blanco;
  padding: 2rem;
  border-radius: 1rem;
}

.servicio {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 5rem;
  @include m.tablet {
    padding: 0 2rem;
    display: grid;
  }
}

.servicio h3 {
  color: v.$secundario;
  font-weight: bold;
  text-align: center;
}

.servicio p {
  line-height: 1.5;
  text-align: center;
}

.servicio .iconos {
  height: 10rem;
  width: 10rem;
  background-color: v.$primario;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  .icon {
    height: 3.5rem;
    @include m.tablet {
      height: 4.5rem;
      width: auto;
      transition: height 500ms linear;
      &:hover {
        height: 6rem;
        transition: height 500ms linear;
      }
    }
  }
  @include m.tablet {
    height: 15rem;
    width: 15rem;
  }
}

@include m.tablet {
  .servicios {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr; /* 1fr = fraction repeat(3, 1fr) */
    column-gap: 2rem;
  }
}
