@use "base/variables" as v;
@use 'base/mixins' as m;

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}

.checkmark__circle {
  stroke-dasharray: 166;
  stroke-dashoffset: 166;
  stroke-width: 4;
  stroke-miterlimit: 10;
  stroke: v.$secundario;
  fill: none;
  animation: stroke 0.6s cubic-bezier(0.65, 0, 0.45, 1) forwards;
}

.checkmark {
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  display: block;
  stroke-width: 2;
  stroke: #fff;
  stroke-miterlimit: 10;
  margin: 10% auto;
  box-shadow: inset 0px 0px 0px v.$secundario;
  animation: fill 0.4s ease-in-out 0.4s forwards,
    scale 0.3s ease-in-out 0.9s both;
  @include m.tablet {
      width: 10rem;
      height: 10rem;
      margin: 5% auto;
  }
}

.checkmark__check {
  transform-origin: 50% 50%;
  stroke-dasharray: 48;
  stroke-dashoffset: 48;
  animation: stroke 0.3s cubic-bezier(0.65, 0, 0.45, 1) 0.8s forwards;
}

.thanks-msg {
  text-align: center;
  padding: 0 2rem;
  margin-bottom: 2rem;
  h2 {
    font-size: 3rem;
    font-weight: bold;
    margin: 0;
    margin-bottom: 1rem;
    @include m.tablet {
        font-size: 4rem;
        margin-bottom: 6rem;
    }
  }
  @include m.tablet {
      padding: 0 0;
      margin-bottom: 0;
  }
}

.thanks-phone{
    margin-top: 4rem;
}

.thanks-home{
    font-weight: bold;
    &:hover{
        color: v.$primario;
    }
}

.thanks-section {
  margin-top: auto;
  margin-bottom: auto;
}

@keyframes stroke {
  100% {
    stroke-dashoffset: 0;
  }
}

@keyframes scale {
  0%,
  100% {
    transform: none;
  }

  50% {
    transform: scale3d(1.1, 1.1, 1);
  }
}

@keyframes fill {
  100% {
    box-shadow: inset 0px 0px 0px 60px v.$secundario;
  }
}
