@use "base/variables" as v;
@use "base/mixins" as m;

.nav-bg {
  background-color: v.$secundario;
}

.navegacion-principal {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.blue-bar{
  background-color: v.$secundario;
  height: 4rem;
  @include m.tablet {
    height: 4.5rem;
  }
}

@include m.tablet {
  .navegacion-principal {
    flex-direction: row;
    justify-content: space-around;
  }
}

.navegacion-principal a {
  display: block;
  text-align: center;
  color: v.$blanco;
  text-decoration: none;
  font-size: 1.5rem;
  font-weight: bold;
  padding: 1rem;
  @include m.tablet {
    font-size: 2rem;
  }
}

.navegacion-principal a:hover {
  background-color: v.$primario;
  color: v.$oscuro;
}