@use "base/mixins" as m;
@use "base/variables" as v;

.titulo {
  .titulo-guion {
    display: none;
  }
  .titulo-texto {
    margin-top: 1rem;
    display: block;
  }
  @include m.tablet {
    .titulo-guion{
      display: inline;
    }
    .titulo-texto{
      margin-top: 0;
      display: inline;
    }
    // border-right: solid 3px v.$secundario;
    // white-space: nowrap;
    // overflow: hidden;
    margin-left: auto;
    margin-right: auto;
    // animation: animated-text 5s steps(29, end) 1s 1 normal both,
    //   animated-cursor 1000ms steps(29, end) infinite;
  }
  // span {
  //   font-size: 2rem;
  //   display: block;
  //   margin-top: 2rem;
  // }
}

header {
  margin-top: 2rem;
  margin-bottom: 1rem;
  h1 {
    font-size: 1.3rem;
  }
  .header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  img {
    width: 25vw;
  }
  @include m.telefono {
    h1 {
      font-size: 1.5rem;
      padding-left: 1rem;
    }
    .header {
      flex-direction: row;
    }
  }
  @include m.tablet {
    h1 {
      font-size: 2rem;
      text-align: start;
      padding-left: 3rem;
    }
    span {
      display: block;
    }
    img {
      width: 15rem;
    }
  }
  @include m.desktop {
    margin-top: 3rem;
    margin-bottom: 2rem;
  }
}